// src/lib/auth.js
import { cookies } from "next/headers"
import { NextResponse } from "next/server"

const AUTH_COOKIE_NAME = "app-auth"
const DEFAULT_MAX_AGE = 60 * 60 * 24 * 7 // 7 days

function encodeUserPayload(user) {
  return Buffer.from(JSON.stringify(user), "utf8").toString("base64url")
}

function decodeUserPayload(payload) {
  try {
    return JSON.parse(Buffer.from(payload, "base64url").toString("utf8"))
  } catch (error) {
    console.warn("[auth] Failed to decode user payload", error)
    return null
  }
}

export async function getCurrentUser() {
  const cookieStore = await cookies()
  const sessionCookie = cookieStore.get(AUTH_COOKIE_NAME)
  if (!sessionCookie?.value) {
    return null
  }

  return decodeUserPayload(sessionCookie.value)
}

export function createLoginResponse(user, { message = "Authenticated", status = 200, extra = {}, maxAge = DEFAULT_MAX_AGE } = {}) {
  const response = NextResponse.json({ user, message, ...extra }, { status })
  response.cookies.set({
    name: AUTH_COOKIE_NAME,
    value: encodeUserPayload(user),
    httpOnly: true,
    sameSite: "lax",
    secure: process.env.NODE_ENV === "production",
    path: "/",
    maxAge,
  })
  return response
}

export function createLogoutResponse({ message = "Logged out", status = 200 } = {}) {
  const response = NextResponse.json({ ok: true, message }, { status })
  response.cookies.set({
    name: AUTH_COOKIE_NAME,
    value: "",
    httpOnly: true,
    sameSite: "lax",
    secure: process.env.NODE_ENV === "production",
    path: "/",
    expires: new Date(0),
  })
  return response
}

export function buildUnauthenticatedResponse({ message = "Not authenticated", status = 401 } = {}) {
  return NextResponse.json({ user: null, message }, { status })
}

export { AUTH_COOKIE_NAME }
